	import share from '@/components/share';
	export default{
		components: {
			share
		},
		data() {
			return {
				oProduct: {},
				dPrice: 0.0,
				iInventory: 0,
				bFavorite: true,
				aShareList: [],
				aImgList: [],
				aSpecs: [],
				sSpecClass: 'none',
				aSpecSelected: [],
/*
				desc: `
					<div style="width:100%">
						<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
						<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
					</div>
				`,
				specList: [
					{
						id: 1,
						name: '尺寸',
					},
					{	
						id: 2,
						name: '颜色',
					},
				],
				specChildList: [
					{
						id: 1,
						pid: 1,
						name: 'XS',
					},
					{
						id: 2,
						pid: 1,
						name: 'S',
					},
					{
						id: 3,
						pid: 1,
						name: 'M',
					},
					{
						id: 4,
						pid: 1,
						name: 'L',
					},
					{
						id: 5,
						pid: 1,
						name: 'XL',
					},
					{
						id: 6,
						pid: 1,
						name: 'XXL',
					},
					{
						id: 7,
						pid: 2,
						name: '白色',
					},
					{
						id: 8,
						pid: 2,
						name: '珊瑚粉',
					},
					{
						id: 9,
						pid: 2,
						name: '草木绿',
					},
				]
*/
			};
		},
		async onLoad(options){

			//接收传值,id里面放的是标题，因为测试数据并没写id 
			let id = options.id;
//			if(id){
//				this.$api.msg(`点击了${id}`);
//			}

			await this.$store.dispatch('getProduct', {
				"id": id
			}).then((oProduct) => {
				this.oProduct = oProduct;
				if (oProduct.image1url && oProduct.image1url !== '') this.aImgList.push(oProduct.image1url);
				if (oProduct.image2url && oProduct.image2url !== '') this.aImgList.push(oProduct.image2url);
				if (oProduct.image3url && oProduct.image3url !== '') this.aImgList.push(oProduct.image3url);
				if (oProduct.image4url && oProduct.image4url !== '') this.aImgList.push(oProduct.image4url);
				if (oProduct.image5url && oProduct.image5url !== '') this.aImgList.push(oProduct.image5url);
				if (oProduct.marketprice && oProduct.marketprice !== 0 && oProduct.baseprice !== oProduct.marketprice)
					this.oProduct.discount = (oProduct.baseprice / oProduct.marketprice * 10).toFixed(1);
				this.dPrice = oProduct.baseprice;
				this.iInventory = oProduct.inventory;
				if (!oProduct.productdetails) this.oProduct.productdetails[0].imageurl = oProduct.image1url;
			});

			//整理规格
			if (this.oProduct.productspecs && this.oProduct.productspecs.length > 0) {
				let i = 0;
				let sSpecName = this.oProduct.productspecs[0].specname;
				this.aSpecs[i] = {};
				this.aSpecs[i].specname = sSpecName;
				this.aSpecs[i].specs = [];
				for (let oSpec of this.oProduct.productspecs) {
					if (oSpec.specname !== sSpecName) {
						i++;
						sSpecName = oSpec.specname;
						this.aSpecs[i] = {};
						this.aSpecs[i].specname = sSpecName;
						this.aSpecs[i].specs = [];
					}
					this.$set(oSpec, 'selected', false);
					this.aSpecs[i].specs.push(oSpec);
				}
			}
//			this.specList.forEach(item=>{
//				for(let cItem of this.specChildList){
//					if(cItem.pid === item.id){
//						this.$set(cItem, 'selected', true);
//						this.aSpecSelected.push(cItem);
//						break; //forEach不能使用break
//					}
//				}
//			})
			this.aShareList = await this.$api.json('aShareList');
		},
		methods:{
			//规格弹窗开关
			toggleSpec() {
				if(this.sSpecClass === 'show'){
					this.sSpecClass = 'hide';
					setTimeout(() => {
						this.sSpecClass = 'none';
					}, 250);
				}else if(this.sSpecClass === 'none'){
					this.sSpecClass = 'show';
				}
			},
			//选择规格
			selectSpec(iValueIndex, iSpecIndex){
				let list = this.aSpecs[iSpecIndex].specs;
				list.forEach(item=>{
					if(item.selected === true){
						this.$set(item, 'selected', false);
					}
				})

				this.$set(list[iValueIndex], 'selected', true);
				this.dPrice = this.oProduct.baseprice;
				this.iInventory = this.oProduct.inventory;

				//存储已选择
				this.aSpecSelected = [];
				this.aSpecs.forEach(aSpec=>{
					aSpec.specs.forEach(item=>{ 
						if(item.selected === true){ 
							this.aSpecSelected.push(item); 
							this.dPrice = item.price ? this.dPrice + item.price : this.dPrice;
							this.iInventory = item.inventory && item.inventory < this.iInventory ? item.inventory : this.iInventory;
						} 
					})
				})
				if (this.oProduct.marketprice && this.oProduct.marketprice !== 0 && this.dPrice !== this.oProduct.marketprice) {
					this.oProduct.discount = (this.dPrice / this.oProduct.marketprice * 10).toFixed(1);
				} else {
					this.oProduct.discount = 0;
				}
				
			},
			//分享
			share(){
				this.$refs.share.toggleMask();	
			},
			//收藏
			toFavorite(){
				this.bFavorite = !this.bFavorite;
			},
			buy(){
				uni.navigateTo({
					url: `/pages/order/createOrder`
				})
			},
			stopPrevent(){}
		},

	}
